<template>
  <div class="tmpl">
    <nav-bar title="图文详情"></nav-bar>
    <div class="photo-title">
      <p v-text="photoDetail.img_title"></p>
      <div class="photo-info">
        <span>点击：{{photoDetail.click_num}}</span>
        <span>分类：{{photoDetail.title}}</span>
        <span>发布时间：{{photoDetail.add_time|filterTime}}</span>
      </div>
    </div>
    <img class="photo-img" :src="photoDetail.img">
    <div class="photo-content" v-html="photoDetail.content"></div>
    <!--评论-->
    <comment category="photo" :cid="cid"></comment>
  </div>
</template>

<script>
    export default {
      name: "PhotoDetail",
      data(){
        return{
          photoDetail:{},
          cid:this.$route.params.id,//当前图片详情ID
        }
      },
      created(){
        // 发起请求
        this.$axios.get(this.$host+'/api/getimagedetail/'+this.cid)
          .then(res=>{
            console.log(res.data.data);
            this.photoDetail = res.data.data;
          })
          .catch(err=>{
            console.log(err);
          })
      }
    }
</script>

<style scoped>
  .photo-title{
    margin-top: 5px;
    border-bottom: 1px solid rgba(0,0,0,0.2);
  }
  .photo-title p{
    color: #0a87f8;
    font-size: 20px;
    font-weight: bold;
  }
  .photo-title span{
    margin-right: 30px;
  }
  .photo-info{
    font-size: 14px;
  }
  .photo-img{
    width: 370px;
    height: 300px;
  }
  /*主体文章的左右标题*/
  .photo-content{
    padding: 5px;
  }
</style>



